.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.5;
  border-radius: inherit;
}

/*------------------------------------
  Waves
------------------------------------*/
.waves-top-center {
  position: absolute;
  top: -1px;
  left: 0;
  right: 0;
  bottom: auto;
}

.waves-bottom-center {
  position: absolute;
  bottom: -1px;
  left: 0;
  right: 0;
  top: auto;
}

.waves-top-start {
  position: absolute;
  top: -1px;
  left: 0;
}

.waves-top-end {
  position: absolute;
  top: -1px;
  right: 0;
}

.waves-bottom-start {
  position: absolute;
  bottom: -1px;
  left: 0;
}

.waves-bottom-end {
  position: absolute;
  bottom: -1px;
  right: 0;
}

.translate-top-1 {
  transform: translate(0, -1rem);
}

.translate-bottom-1 {
  transform: translate(0, 1rem);
}

.translate-top-2 {
  transform: translate(0, -2rem);
}

.translate-bottom-2 {
  transform: translate(0, 2rem);
}

.translate-top-3 {
  transform: translate(0, -3rem);
}

.translate-bottom-3 {
  transform: translate(0, 3rem);
}

.translate-top-4 {
  transform: translate(0, -4rem);
}

.translate-bottom-4 {
  transform: translate(0, 4rem);
}

.translate-top-5 {
  transform: translate(0, -5rem);
}

.translate-bottom-5 {
  transform: translate(0, 5rem);
}

@keyframes animateWave {
  0% {
    transform: scale(1, 0);
    -webkit-transform: scale(1, 0);
  }
  100% {
    transform: scale(1, 1);
    -webkit-transform: scale(1, 1);
  }
}

.waves-animate > svg {
  display: block;
  transform-origin: bottom;
  animation: animateWave 3s cubic-bezier(0.23, 1, 0.32, 1) forwards;
  -webkit-animation: animateWave 3s cubic-bezier(0.23, 1, 0.32, 1) forwards;
}

/*------------------------------------
	Responsive maps
------------------------------------*/
.responsive-maps {
  position: relative;
  padding-bottom: 75% !important;
  overflow: hidden;
}

.responsive-maps iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100% !important;
  height: 100% !important;
}

